import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useGetBlogListQuery } from '../api/apislice'
import { Spinners } from '../../app/spinner'

export const BlogsDash = () => {
  const {
          data: bloglist = [], 
          isLoading, 
          isSuccess, 
          isError,
          error
        } = useGetBlogListQuery()
  const [blogs, setBlogs] = useState([])
  const [psdBlogs, setPsdBlogs] = useState(bloglist)
  const [inputValue, setInputValue] = useState('')
  useEffect(()=>{
                  if(isSuccess){
                    setPsdBlogs(bloglist)
                    setBlogs(bloglist)
                  }
  })
    
  const onBlogSelect = e =>{
                              console.log(e.target.value)
                            }
  const onBlogSrc = e => {
    
  if(e.target.value.length > 0){
                                    setInputValue(e.target.value)
                                    const regex = new RegExp(`^${e.target.value}`, 'i')
                                    const foundCIs = bloglist.filter(({title}) => title.match(regex))
                                    setPsdBlogs(foundCIs)
                              }else{
                                setPsdBlogs(blogs)
                                setInputValue('')
                              }
    }
  const content = psdBlogs.slice().sort((a,b)=>{
                                                if (a.date < b.date){
                                                  return -1
                                                }
                                                if (a.date > b.date){
                                                  return 1
                                                }
                                                  return 0
                                              }).map((blog, i)=>(
                                                <div className="row mb-2" key={blog._id}>
                                                    <div className="col-sm-1 text-capitalize">
                                                      <input
                                                        className="form-check-input"
                                                        type="checkbox"
                                                        id={blog._id}
                                                        value={blog._id}
                                                        name={blog._publisher}
                                                        onChange={onBlogSelect}
                                                      />
                                                    </div>
                                                    <div className="col-sm-1 text-capitalize">
                                                      {i+=1}
                                                    </div>
                                                    <div className="col-sm-4 text-capitalize">
                                                      {blog.title}
                                                    </div>
                                                    <div className="col-sm-3 text-capitalize">
                                                      {(new Date(blog.date)).toUTCString()}
                                                    </div>
                                                    <div className="col-sm-2 text-capitalize">
                                                      {blog.approved ? 'Approved': 'Pending Approval'}
                                                    </div>
                                                    <div className="col-sm-1">
                                                      <Link to={`/blogs/view/${blog._id}`}>
                                                        <div
                                                        className='btn btn-info btn-sm w-100'
                                                        >
                                                          View
                                                        </div>
                                                      </Link>
                                                    </div>
                                                </div>
                                                )
                                              )

  return (
            <section className="poll-list">
              <div className="leadtitle">
                <h1>Blogs Board</h1>
              </div>
              <div className="dashboard-block">
                <div className="container">
                  <div className="row mb-5">
                    <div className="col-sm-4">
                      <Link to="/polls/create"><div className="btn btn-success">Blogs</div></Link>
                    </div>
                    <div className="col-sm-4">
                      <input
                        className="form-control mx-2"
                        placeholder="Search Blogs"
                        onChange={onBlogSrc}
                        value={inputValue}
                      />
                    </div>
                    <div className="col-sm-4">
                      <Link to = "/dashboard">
                      <div 
                        className="btn btn-info"
                        
                        >Go to Dashboard</div>
                      </Link>
                    </div>
                  </div>
                  {content}
                </div>
              </div>
            </section>
          )
}
