import React from 'react'
import { Users } from '../users/users'
import { Polls } from '../polls/polls'
import { Parties } from '../party/parties'
import { Candidates } from '../candidates/candidates'
import { Respondents } from '../users/respondent'
import { Blogs } from '../blogs/blogs'




export const Dashboard = (props) => {
  return (
    <section className="poll-list">
      <div className="leadtitle">
        <h1>Pollit Admin Board</h1>
      </div>
      <div className="dashboard-block">
        <div className="container">
          <div className="d-flex row">
            <div className=" d-flex col justify-content-center"><Users/></div>
            <div className="d-flex col justify-content-center"><Polls/></div>
            <div className="w-100"></div>
            <div className="d-flex col justify-content-center"><h1><Candidates/></h1></div>
            <div className="d-flex col justify-content-center"><h1><Blogs/></h1></div>
            <div className="w-100"></div>
            <div className="d-flex col justify-content-center"><h1><Parties/></h1></div>
            <div className="d-flex col justify-content-center"><h1><Respondents/></h1></div>
          </div>
        </div>
      </div>
    </section>
  )
}
