import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'


const initialState = {
                        reports       : {
                                            eventCount:0,
                                            events:[],
                                            activeEvent:{}
                                          },
                        token         : null,                                          
                        status        : 'idle',
                        error         : null
                      }
export const fetchReport = createAsyncThunk('reports/fetchReport', async (params, {getState, dispatch}) => {
   let token = getState().auth.token 
      const init =  {
                      method: 'GET',
                      headers:  {
                                  'authorization': `Bearer ${token}`
                                }
                    }
        const response = await fetch(`${process.env.ServerURL}/reports`, init)
          if (response.ok){
            const jsonValue = await response.json()
            return Promise.resolve(jsonValue)
          }
})
export const fetchEvent = createAsyncThunk('reports/fetchEvent', async ( id, {getState, dispatch}) => {
    let token = getState().auth.token
      const init =  {
                      method: 'GET',
                      headers:  {
                                  'authorization': `Bearer ${token}`
                                }
                    }
        const response = await fetch(`${process.env.ServerURL}/event/${id}`, init)
          if (response.ok){
            const jsonValue = await response.json()
            return Promise.resolve(jsonValue)
          }
})
const reportsSlice = createSlice({
                                      name: 'reports',
                                      initialState,
                                      reducers: {
                                                    purgeEvent:{
                                                      reducer(state, action){
                                                        state.reports.activeEvent = {}
                                                      }
                                                    }
                                                },
                                      extraReducers(builder) {
                                                  builder
                                                  .addCase(fetchReport.pending, (state, action) => {
                                                    state.status = 'loading'
                                                  })
                                                  .addCase(fetchReport.fulfilled, (state, action) => {
                                                    state.status = 'succeeded'
                                                    state.reports.concat(action.payload)
                                                  })
                                                  .addCase(fetchReport.rejected, (state, action) => {
                                                    state.status = 'failed'
                                                    state.error = action.error.message
                                                  })
                                                  .addCase(fetchEvent.pending, (state, action) => {
                                                    state.status = 'loading'
                                                  })
                                                  .addCase(fetchEvent.fulfilled, (state, action) => {
                                                    state.status = 'succeeded'
                                                    state.reports.activeEvent = action.payload
                                                  })
                                                  .addCase(fetchEvent.rejected, (state, action) => {
                                                    state.status = 'failed'
                                                    state.error = action.error.message
                                                  })
                                                }
                                  })

export const { selectReport } = reportsSlice.actions;

export default reportsSlice.reducer

export const reportsCall = state => state.reports.reports.eventCount
