import { useAuth0 } from "@auth0/auth0-react";
import React from "react";
import { persistor } from '../../app/store'

const AuthButton = () => {
  const { user, isAuthenticated, loginWithRedirect, logout } = useAuth0();
  if (!isAuthenticated){
    return <button className="btn btn-link text-dark text-decoration-none" onClick={() => {
      persistor.purge()
      loginWithRedirect()
    }}>
        Log In</button>;
  }else{
    return (
      <button className="btn btn-link btn-sm text-dark text-decoration-none" onClick={() =>{
        persistor.purge()
        logout({ returnTo: window.location.origin })
      }}
      >
        Log Out
      </button>
    )
  }
}

export default AuthButton