import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import { useGetRespondentCountQuery } from '../api/apislice' 

export const Respondents = () => {
  const [userCount, setUserCount] = useState(0)
  const {
    data: respondents,
    isLoading,
    isSuccess,
    isError,
    error
} = useGetRespondentCountQuery()

useEffect(()=>{
  if(isSuccess){
                  setUserCount(respondents.count)
  }
})

return(
          <div className="poll-list">
            <div>
              <h4>Respondents</h4>
            </div>
            <div className="dash-numbers">
            <Link to="/respondent/dashboard"><h2>{userCount}</h2></Link>
            </div>
          </div>
        )
}
