import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { selectToken } from '../auth/authSlice'
import { activeCand, updateCandidateOffice, updateCandidateOfficeId, updateCandidateOfficeScope, updateCandidateOfficeScopeId } from './candidatesSlice'

export const Office = () =>{
  const candidate = useSelector(activeCand)
  const authToken = useSelector(selectToken)
  const [office, setOffice] = useState('')
  const [officeId, setOfficeId] = useState('')
  const [officeScopeId, setOfficeScopeId] = useState('')
  const [officeScope, setOfficeScope] = useState('')
  const [placeholder, setPlaceholder] = useState('')
  const [span, setSpan] = useState('hidden')
  const [inputVis, setInputVis] = useState('hidden')
  const [buttonVis, setButtonVis] = useState('hidden')
  const [inputValue, setInputValue] = useState('')
  const [cstts, setCstts] = useState([])
  const [psdCstts, setPsdCstts] = useState([])
  const dispatch = useDispatch()
  const onCsttChange = e => {
    if(e.target.value.length > 0){
                                setInputValue(e.target.value)
                                setSpan('')
                                const regex = new RegExp(`^${e.target.value}`, 'i')
                                const foundCstts = cstts.filter(({name}) => name.match(regex))
                                setPsdCstts(foundCstts)
                                if(cstts.some(({name})=> name.toLowerCase() === e.target.value.toLowerCase())){
                                  setOfficeScopeId(foundCstts[0]._id)
                                  setOfficeScope(e.target.value.toLowerCase())
                                  setPsdCstts([])
                                }else{
                                  setOfficeScope('')
                                }
                              }else{
                                setInputValue('')
                                setPsdCstts([])
                                setSpan('hidden')
                              }
} 
const onSelectCstt = e => {
                              setInputValue(e.target.innerHTML)
                              setPsdCstts([])
                              setSpan('hidden')
                              if(cstts.some(({name})=> name === e.target.innerHTML) ){
                                setOfficeScopeId(e.target.dataset.id)
                                setOfficeScope(e.target.innerHTML.toLowerCase())
                              }
                            }
  const parsedCstts = psdCstts.map(cstt =>(
    <span className="parsedobjects-items" key={cstt._id} data-id={cstt._id} type="button"
    onClick = {onSelectCstt}
    >
      {cstt.name}
    </span>
  ))
  
  const onInputChange = e => {
                                setInputValue('')
                                setInputVis('hidden')
                                setSpan('hidden')
                                setOffice(e.target.value)
                                setOfficeId(e.target.dataset.officeid)
                                setButtonVis('')
                                if(e.target.value !== 'presidential'){ 
                                  const init = {
                                    method: 'GET',
                                    headers:  {
                                                'Content-Type':'application/x-www-form-urlencoded',
                                                'authorization': `Bearer ${authToken}`
                                              }
                                  }
                                  setInputVis('')
                                  setPlaceholder(e.target.dataset.pl)
                                  fetch(`${process.env.ServerURL}/${e.target.value}/sr/${candidate.stateId}`, init)
                                    .then((response)=> response.json())
                                    .then(fcstts => {
                                        setCstts(fcstts)
                                    })
                                }else{
                                        setOfficeScopeId('x06PR0112plx06PR0112pl')
                                        setOfficeScope('presidential')
                                }
                              }
  const onSubmitOffice = () =>{
                                  dispatch(updateCandidateOffice(office))
                                  dispatch(updateCandidateOfficeId(officeId))
                                  dispatch(updateCandidateOfficeScope(officeScope))
                                  dispatch(updateCandidateOfficeScopeId(officeScopeId))
                                }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
  })
  return(
    <div
      id="form-body"
      className="form-body">
      <h1 className="form-header">Which office is candidate vying for?</h1>
      <div className="form-section">
      <div className="d-flex flex-column">
      <div className="custom-control custom-radio custom-control-inline mx-2">
        <input
          type="radio"
          id="presidential"
          data-officeid="x06PR0112pl"
          data-pl="country"
          name="office"
          value="presidential"
          className="custom-control-input"
          onChange ={onInputChange}
          />
        <label className="custom-control-label" htmlFor="presidential">Presidential</label>
      </div>
      <div className="custom-control custom-radio custom-control-inline mx-2">
        <input
          type="radio"
          id="senate"
          data-officeid="x05SE0306se"
          data-pl="federal constituency"
          name="office"
          value="senate"
          className="custom-control-input"
          onChange ={onInputChange}
          />
        <label className="custom-control-label" htmlFor="senate">Senate</label>
      </div>
      <div className="custom-control custom-radio custom-control-inline mx-2">
        <input
          type="radio"
          id="houseOfRep"
          data-officeid="x04HRxx10hp"
          data-pl="federal constituency"
          name="office"
          value="houseOfRep"
          className="custom-control-input"
          onChange ={onInputChange}
          />
        <label className="custom-control-label" htmlFor="houseOfRep">House of Rep</label>
      </div>
      <div className="custom-control custom-radio custom-control-inline mx-2">
        <input
          type="radio"
          id="governorship"
          data-officeid="x03GO0112gp"
          data-pl="state"
          name="office"
          value="governorship"
          className="custom-control-input"
          onChange ={onInputChange}
          />
        <label className="custom-control-label" htmlFor="governorship">Governorship</label>
      </div>
      <div className="custom-control custom-radio custom-control-inline mx-2">
        <input
          type="radio"
          id="stateRep"
          data-officeid="x02SRxx08sp"
          data-pl="state constituency"
          name="office"
          value="stateRep"
          className="custom-control-input"
          onChange ={onInputChange}
          />
        <label className="custom-control-label" htmlFor="stateRep">State Rep</label>
      </div>
      <div className="custom-control custom-radio custom-control-inline mx-2">
        <input
          type="radio"
          id="localGovt"
          data-officeid="x01LGxx09lt"
          data-pl="local government"
          name="office"
          value="localGovt"
          className="custom-control-input"
          onChange ={onInputChange}
          />
        <label className="custom-control-label" htmlFor="localGovt">Local Govt</label>
      </div>
      </div>
      <div className="d-flex flex-column justify-content-end">
      
      </div>
      </div>
      <div className="form-section">
      <input
        className="form-control mx-2"
        onChange={onCsttChange}
        placeholder={placeholder}
        hidden={inputVis}
        value = {inputValue}
      />
      <button
        type="button"
        className="btn btn-success"
        hidden={buttonVis}
        onClick = {onSubmitOffice}
        >Submit</button>
      </div>
      <div className="parsedobjects" hidden={span}>{parsedCstts}</div>
    </div>
  )
}
