import React, { useState, useEffect, useSelector } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import { loggedAdmin } from '../users/usersSlice'
import { purgeActiveCand, verifyCandidate} from './candidatesSlice'
import { selectToken } from '../auth/authSlice'





export const CandDash = (props) => {
  const admin = useSelector(loggedAdmin)
  const authToken = useSelector(selectToken)
  const [inputValue, setInputValue] = useState('')
  const [selectedCand, setSelectedCand] = useState('')
  const [candId, setCandId] = useState('')
  const [candidates, setCandidates] = useState([])
  const [psdCands, setPsdCands] = useState([])
  const [lastUpdate, setLastUpdate] = useState('none')
  const [error, setError] = useState({})
  const [verifyError, setVerifyError] =useState({})
  const dispatch = useDispatch() 
  const init = {
                  method: 'GET',
                  headers:  {
                              'Content-Type':'application/x-www-form-urlencoded',
                              'authorization': `Bearer ${authToken}`
                            }
                }
  const onCandSrc = e => {
                          if(e.target.value.length > 0){
                                                      setInputValue(e.target.value)
                                                      const regex = new RegExp(`^${e.target.value}`, 'i')
                                                      const foundCIs = candidates.filter(({_id}) => _id.match(regex))
                                                      setPsdCands(foundCIs)
                                                }else{
                                                      setPsdCands(candidates)
                                                      setInputValue('')
                                                    }

                          }
  const onCandSelect = e =>{
                              console.log(e.target.value)
                            }
  const refreshCand = () => {
                                fetch(`${process.env.ServerURL}/candidates/list`, init)
                                .then((response)=> response.json())
                                .then(fCands => {
                                                  if(fCands.length>0){
                                                                      setCandidates(fCands)
                                                                      setPsdCands(fCands)
                                                                    }
                                                  })
                                .catch(error => {
                                                    setVerifyError(error)
                                                })
                            }
  const vpack = new Object()
                vpack.actionId = admin.uid
                vpack.actionFname = admin.firstName 
                vpack.actionLname = admin.lastName
  const onVerify = e => {
                          vpack.verifyId = e.target.dataset.id
                           let cbody = new URLSearchParams(vpack).toString();
                          const enit = {
                                          method: 'POST',
                                          headers:  {
                                                      'Content-Type':'application/x-www-form-urlencoded',
                                                      'authorization': `Bearer ${authToken}`
                                                    },
                                          body:  cbody
                                        }
                           fetch(`${process.env.ServerURL}/candidate/verify/`, enit)
                                  .then((response)=> response.json())
                                  .then(uCands => {
                                                     setLastUpdate(uCands.update)
                                                     refreshCand()
                                                    })
                        }
  const onSuspend = e => {
                          console.log('suspended')
                          /*vpack.suspendId = e.target.dataset.id
                          dispatch(suspendCandidate(vpack))*/
                        }
  useEffect(()=>{           
                            dispatch(purgeActiveCand())
                            if(!candidates.length>0 ){
                                                      fetch(`${process.env.ServerURL}/candidates/list`, init)
                                                      .then((response)=> response.json())
                                                      .then(fCands => {
                                                                        if(fCands.length>0){
                                                                                            setCandidates(fCands)
                                                                                            setPsdCands(fCands)
                                                                                          }
                                                       })
                                                       .catch(error => {
                                                                          setError(error)
                                                                      })
                                                      }
                   })
  const displayCand = psdCands.map((cand, i)=>(
                                              <div className="row mb-2" key={cand._id}>
                                                  <div className="col-sm text-capitalize">
                                                    <input
                                                      className="form-check-input"
                                                      type="checkbox"
                                                      id={cand._id}
                                                      value={cand._id}
                                                      name={`${cand.uid.firstName} ${cand.uid.lastName}`}
                                                      onChange={onCandSelect}
                                                    />
                                                  </div>
                                                  <div className="col-sm text-capitalize">
                                                    {i+=1}
                                                  </div>
                                                  <div className="col-sm text-capitalize">
                                                  {cand.firstName}
                                                  </div>
                                                  <div className="col-sm text-capitalize">
                                                    {cand.lastName}
                                                  </div>
                                                  <div className="col-sm text-capitalize">
                                                    {cand.partyAbbv}
                                                  </div>
                                                  <div className="col-sm text-capitalize">
                                                    {cand.office}
                                                  </div>
                                                  <div className="col-sm text-capitalize">
                                                    {cand.state}
                                                  </div>
                                                  <div className="col-sm">
                                                    <button
                                                      className={cand.verified ? 'btn btn-warning btn-sm w-100': 'btn btn-success btn-sm w-100'}
                                                      data-id={cand._id}
                                                      data-cstat={cand.verified}
                                                      onClick={cand.verified ? onSuspend : onVerify }
                                                      >
                                                        {cand.verified ? 'Suspend': 'Verify'} 
                                                    </button>
                                                  </div>
                                              </div>
                                            ))
  return (
            <section className="poll-list">
              <div className="leadtitle">
                <h1>Candidates Board</h1>
              </div>
              <div className="dashboard-block">
                <div className="container">
                  <div className="row mb-5">
                    <div className="col-sm">
                      <Link to="/candidate/create"><div className="btn btn-success">New Candidate</div></Link>
                    </div>
                    <div className="col-sm">
                      <input
                        className="form-control mx-2"
                        placeholder="Search Candidate"
                        onChange={onCandSrc}
                        value={inputValue}
                      />
                    </div>
                    <div className="col-sm">
                      <p>lastupdate: <span>{lastUpdate}</span></p>
                    </div>
                  </div>
                  <div className="row mb-5">
                    <div className="col-sm mx-3">
                      {displayCand}
                    </div>
                  </div>
                </div>
              </div>
            </section>
          )
}
