import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import { useGetUserCountQuery } from '../api/apislice' 

export const Users = () => {
  const [userCount, setUserCount] = useState(0)
  const {
    data: users,
    isLoading,
    isSuccess,
    isError,
    error
} = useGetUserCountQuery()

useEffect(()=>{
  if(isSuccess){
                  setUserCount(users.count)
  }
})

return(
          <div className="poll-list">
            <div>
              <h4>All Users</h4>
            </div>
            <div className="dash-numbers">
            <Link to="/users/dashboard"><h2>{userCount}</h2></Link>
            </div>
          </div>
        )
}
