import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useSelector } from 'react-redux' 
import { selectToken } from '../auth/authSlice'

export const NewParty = () =>{
  const authToken = useSelector(selectToken)
  const [partyName, setPartyName] = useState('')
  const [brandName, setBrandName] = useState('')
  const [brandValue, setBrandValue] = useState('')
  const [nameValue, setNameValue] = useState('')
  const [submitBtnVis, setSubmitBtnVis] = useState('')
  const [proceedBtnVis, setProceedBtnVis] = useState('hidden')
  const [formInput, setFormInput] = useState('')
  const [submitSuccess, setSubmitSuccess] = useState('hidden')
  const canSubmit = Boolean(partyName && brandName)
  const onPartyNameChange = e => {
                                    setNameValue(e.target.value)
                                    setPartyName(e.target.value)
                                  }
  const onBrandNameChange = e => {
                                    setBrandValue(e.target.value.toUpperCase())
                                    setBrandName(e.target.value.toUpperCase())
                                  }                                
  const onSubmitName = async () =>{
                              const party = new Object
                                    party.name = partyName
                                    party.abbv = brandName
                              let cbody = new URLSearchParams(party).toString();
                              const init = {
                                              method: 'POST',
                                              headers:  {
                                                          'Content-Type':'application/x-www-form-urlencoded',
                                                          'authorization': `Bearer ${authToken}`
                                                        },
                                              body:  cbody
                                            }
                                        await fetch(`${process.env.ServerURL}/party/create/`, init)
                                              .then((response)=> response.json())
                                              .then(newParty =>{
                                                  setNameValue('')
                                                  setBrandValue('')
                                                  setPartyName('')
                                                  setBrandName('')
                                                  setSubmitSuccess('')
                                                  setFormInput('hidden')
                                                  setSubmitBtnVis('hidden')
                                                  setProceedBtnVis('')
                                              })
                            }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
  })
  return(
            <div
              id="form-body"
              className="form-body">
              <h1 className="form-header">Create Party</h1>
              <div 
                className="form-section"
                hidden={submitSuccess}
                >
                <h5 className="text-success no-wrap">Party Was Successfully Created</h5>
                </div>
              <div 
                className="form-section"
                hidden={formInput}
                >
                <input
                  className="form-control mx-2"
                  placeholder="Party Full Name"
                  onChange={onPartyNameChange}
                  value={nameValue}
                />
              </div>
              <div 
                className="form-section"
                hidden={formInput}
                >
                <input
                  className="form-control mx-2"
                  placeholder="Brand Name"
                  onChange={onBrandNameChange}
                  value={brandValue}
                />
              </div>
              <div className="form-section">
                <button
                  type="button"
                  className="btn btn-success mx-2"
                  disabled = {!canSubmit}
                  onClick = {onSubmitName}
                  hidden ={submitBtnVis}
                  >
                   Submit
                </button>
                <Link
                  to="/party/dashboard"
                  type="button"
                  className="btn btn-success mx-2"
                  hidden = {proceedBtnVis}
                  >
                  Proceed
                  </Link>
              </div>
            </div>
          )
}
