import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useGetPartyCountQuery } from '../api/apislice'


export const Parties = () => {
  const [partyCount, setPartyCount] = useState(0)
  
  const {
    data: party,
    isLoading,
    isSuccess,
    isError,
    error
} = useGetPartyCountQuery()

useEffect(()=>{
    if(isSuccess){
                    setPartyCount(party.partycount)
    }
})
  return(
          <div className="poll-list">
            <div>
              <h4>Political Parties</h4>
            </div>
            <div className="dash-numbers">
            <Link to="/party/dashboard"><h2>{partyCount}</h2></Link>
            </div>
          </div>
        )
}
