import React, { useState, useEffect } from 'react'
import { useDispatch } from 'react-redux'
import { updateCandidateLastName } from './candidatesSlice'

export const LastName = () =>{
  const [lastName, setLastName] = useState('')
  const [inputValue, setInputValue] = useState('')
  const dispatch = useDispatch()
  const onNameChange = e => {
                                setInputValue(e.target.value)
                                setLastName(e.target.value)
                              }
  const onSubmitName = () =>{
    dispatch(updateCandidateLastName(lastName));
  }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
  })
  return(
    <div
      id="form-body"
      className="form-body">
      <h1 className="form-header">What is candidate's last name?</h1>
      <div className="form-section">
      <input
        className="form-control mx-2"
        onChange={onNameChange}
        value={inputValue}
      />
      <button
        type="button"
        className="btn btn-success mx-2"
        onClick = {onSubmitName}
        >
        Submit
        </button>
       </div>
    </div>
  )
}
