import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
const initialState = {
                          userdash: {
                                      admin:  {}
                                    },
                            status: 'idle',
                             error: null
                          }
const usersSlice = createSlice({
  name: 'users',
  initialState,
  reducers: {
                createAdmin:{
                              reducer(state, action){
                                                      state.userdash.admin = action.payload
                                                    }
                                    },
                purgeUser:{
                      reducer(state, action){
                                              state.userdash.activeuser = {}
                                            }
                            }
                          
            }
          });

export const { createAdmin, purgeUser, resetUser } = usersSlice.actions;

export default usersSlice.reducer
export const loggedAdmin = state => state.users.userdash.admin
export const adminStatus = state => state.users.status 