import React from 'react'
import { useSelector } from 'react-redux'
import { activeCand } from './candidatesSlice'
import { Office } from './office'
import { Party } from './party'
import { LocalGovt } from './localgovt'
import { State } from './state'
import { FirstName } from './firstname'
import { LastName } from './lastname'
import { Attestation } from './attestation'

export const CandSignUp = () => {
 const regCand = useSelector(activeCand)


 var form;
if(!regCand['firstName']){ 
  form = <FirstName/>
}else if (!regCand['lastName']){
  form = <LastName/>
}else if (!(regCand['partyAbbv'] && regCand['partyName'])){
   form = <Party/>
}else if(!(regCand['state'] && regCand['stateId'])){
   form = <State/>
}else if(!(regCand['localGovt'] && regCand['localGovtId'])){
  form = <LocalGovt/>
}else if(!regCand['office']){
  form = <Office/>
}else{
  form = <Attestation/> 
}


  return(
        <div>{form}</div>
  )
}
