import React, { useState, useEffect } from 'react'
import { useSelector } from 'react-redux'
import { useDispatch } from 'react-redux'
import { updateCandidatePartyName, updateCandidatePartyAbbv } from './candidatesSlice'
import { selectToken } from '../auth/authSlice'

export const Party = () =>{
  const [partyName, setPartyName] = useState('')
  const [partyAbbv, setPartyAbbv] = useState('')
  const authToken = useSelector(selectToken)
  const [parties, setParties] =  useState([])
  const [psdParties, setPsdParties] = useState([])
  const [span, setSpan] = useState('hidden')
  const [inputValue, setInputValue] = useState('')  
  const [error, setError] = useState({})
  const dispatch = useDispatch()
  const onPartyChange = e => {
                                 if(e.target.value.length > 0){
                                  setInputValue(e.target.value.toUpperCase()) 
                                  setSpan('')
                                  const regex = new RegExp(`^${e.target.value}`, 'i')
                                  const orderedParties = parties.slice().sort((a,b)=>{
                                                                                        if (a.abbv < b.abbv){
                                                                                          return -1
                                                                                        }
                                                                                        if (a.abbv > b.abbv){
                                                                                          return 1
                                                                                        }
                                                                                        return 0
                                                                                        })
                                  const foundParties = orderedParties.filter(({abbv}) => abbv.match(regex))
                                  setPsdParties(foundParties)
                                  if(parties.some(({abbv})=> abbv === e.target.value.toUpperCase()) && foundParties.length === 1){
                                    setPartyName(foundParties[0].name)
                                    setPartyAbbv(foundParties[0].abbv)
                                    setPsdParties([])
                                  }else{
                                    setPartyName('')
                                    setPartyAbbv('')
                                  }
                                }else{
                                  setInputValue('')
                                  setPsdParties([])
                                  setSpan('hidden')
                                }
                              }
  const onSelectParty = e => {
                              setInputValue(e.target.dataset.abbv.toUpperCase())
                              setPsdParties([])
                              setSpan('hidden')
                              if(parties.some(({abbv})=> abbv === e.target.dataset.abbv) ){
                                  setPartyName(e.target.dataset.name)
                                  setPartyAbbv(e.target.dataset.abbv.toUpperCase())
                              }
                          }
  const onSubmitName = () =>{
                              dispatch(updateCandidatePartyAbbv(partyAbbv))
                              dispatch(updateCandidatePartyName(partyName))
                            }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
    if(!parties.length>0){  
                          const init = {
                                          method: 'GET',
                                          headers:  {
                                                      'Content-Type':'application/x-www-form-urlencoded',
                                                      'authorization': `Bearer ${authToken}`
                                                    }
                                        }
                            fetch(`${process.env.ServerURL}/parties/list`, init)
                            .then((response)=> response.json())
                            .then(fparties => {
                                setParties(fparties)
                            })
                            .catch(error => {
                                              setError(error)
                                          })
    }
  })
  const parsedParties = psdParties.map(prty =>(
    <span className="parsedobjects-items" key={prty._id} data-id={prty._id} data-abbv={prty.abbv} data-name={prty.name} type="button"
    onClick = {onSelectParty}
    >
      {`${prty.abbv} - ${prty.name}`}
    </span>
  ))
  return(
    <div
      id="form-body"
      className="form-body">
      <h1 className="form-header">Which party flag will candidate bear?</h1>
      <div className="form-section">
      <input
        className="form-control mx-2"
        onChange={onPartyChange}
        value={inputValue}
      />
      <button
        type="button"
        className="btn btn-success mx-2"
        onClick = {onSubmitName}
        >
        Submit
        </button>
       </div>
       <div className="parsedobjects" hidden={span}>{parsedParties}</div>
    </div>
  )
}
