import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import { useGetPollCountQuery } from '../api/apislice'


export const Polls = () => {
  const [pollCount, setPollCount] = useState(0)
  const {
    data: polls,
    isLoading,
    isSuccess,
    isError,
    error
} = useGetPollCountQuery()

useEffect(()=>{
    if(isSuccess){
                    setPollCount(polls.pollcount)
    }
})

return(
          <div className="poll-list">
            <div>
              <h4>Active in Poll</h4>
            </div>
            <div className="dash-numbers">
            <Link to="/polls/dashboard"><h2>{pollCount}</h2></Link>
            </div>
          </div>
        )
}
