import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'

const nullCand = {
                      uid:              null,
                      office:           null,
                      officeId:         null,
                      officeScope:      null,
                      officeScopeId:    null,
                      partyAbbv:        null,
                      partyName:        null,
                      state:            null,
                      stateId:          null,
                      localGovt:        null,
                      localGovtId:      null,
                      verified:         false,
                      selfservice:      true,
                      endorsement:      0
                  }

const initialState = {
                        candidatedash : {
                                          activecand  : nullCand,
                                          lastupdate  : {update:'none'}
                                        },
                        status        : 'idle',
                        error         : null
                      }

export const updateCandidate = createAsyncThunk('candidates/updateCandidate', async (params, {getState, dispatch}) => {
  let cbody = new URLSearchParams(params).toString();
  let token = getState().auth.token
  const init = {
                  method: 'POST',
                  headers:  {
                              'Content-Type':'application/x-www-form-urlencoded',
                              'authorization': `Bearer ${token}`
                            },
                  body:  cbody
                }
  const response = await fetch(`${process.env.ServerURL}/candidate/create`, init)
                            if(response.ok){
                              const jsonValue = await response.json()
                              return Promise.resolve(jsonValue)
                            }
})
export const verifyCandidate = createAsyncThunk('candidates/verifyCandidate', async (params, {getState, dispatch}) => {
  let cbody = new URLSearchParams(params).toString();
  let token = getState().auth.token
  const init = {
                  method: 'POST',
                  headers:  {
                              'Content-Type':'application/x-www-form-urlencoded',
                              'authorization': `Bearer ${token}`
                            },
                  body:  cbody
                }
  const response = await fetch(`${process.env.ServerURL}/candidate/verify/`, init)
                            if(response.ok){
                              thunkAPI.dispatch(fetchPollCandCount())
                              const jsonValue = await response.json()
                              return Promise.resolve(jsonValue)
                            }
})
const candidatesSlice = createSlice({
                                      name: 'candidates',
                                      initialState,
                                      reducers: {
                                                    declareCandidate:{
                                                      reducer(state, action){
                                                        state.candidatedash.activecand.uid = action.payload.toString()
                                                      }
                                                    },
                                                    updateCandidateFirstName:{
                                                      reducer(state, action){
                                                        state.candidatedash.activecand.firstName = action.payload
                                                        }
                                                      },
                                                    updateCandidateLastName:{
                                                        reducer(state, action){ 
                                                          state.candidatedash.activecand.lastName = action.payload
                                                          }
                                                        },
                                                    updateCandidatePartyAbbv:{
                                                      reducer(state, action){
                                                        state.candidatedash.activecand.partyAbbv = action.payload
                                                        }
                                                      },
                                                      updateCandidatePartyName:{
                                                        reducer(state, action){
                                                          state.candidatedash.activecand.partyName = action.payload
                                                          }
                                                        },
                                                    updateCandidateOffice:{
                                                          reducer(state, action){
                                                            state.candidatedash.activecand.office = action.payload
                                                          }
                                                        },
                                                    updateCandidateOfficeId:{
                                                      reducer(state, action){
                                                        state.candidatedash.activecand.officeId = action.payload
                                                      }
                                                    },
                                                    updateCandidateOfficeScope:{
                                                      reducer(state, action){
                                                        state.candidatedash.activecand.officeScope = action.payload
                                                      }
                                                    },
                                                    updateCandidateOfficeScopeId:{
                                                      reducer(state, action){
                                                        state.candidatedash.activecand.officeScopeId = action.payload
                                                      }
                                                    },
                                                    updateCandidateState:{
                                                              reducer(state, action){
                                                                state.candidatedash.activecand.state = action.payload
                                                              }
                                                            },
                                                    updateCandidateStateId:{
                                                                      reducer(state, action){
                                                                        state.candidatedash.activecand.stateId = action.payload
                                                                      }
                                                                    },
                                                    updateCandidateLocalGovt:{
                                                          reducer(state, action){
                                                            state.candidatedash.activecand.localGovt = action.payload
                                                          }
                                                        },
                                                    updateCandidateLocalGovtId:{
                                                              reducer(state, action){
                                                                state.candidatedash.activecand.localGovtId = action.payload
                                                              }
                                                            },
                                                    purgeActiveCand:{
                                                              reducer(state, action){
                                                                  state.candidatedash.activecand = nullCand
                                                              }
                                                    }
                                                },
                                      extraReducers(builder) {
                                                  builder
                                                  .addCase(updateCandidate.pending, (state, action) => {
                                                    state.status = 'loading'
                                                  })
                                                  .addCase(updateCandidate.fulfilled, (state, action) => {
                                                    state.status = 'succeeded'
                                                    state.candidatedash.activecand = action.payload
                                                  })
                                                  .addCase(updateCandidate.rejected, (state, action) => {
                                                    state.status = 'failed'
                                                    state.error = action.error.message
                                                  })
                                                  .addCase(verifyCandidate.pending, (state, action) => {
                                                    state.status = 'loading'
                                                  })
                                                  .addCase(verifyCandidate.fulfilled, (state, action) => {
                                                    state.status = 'succeeded'
                                                    state.candidatedash.lastupdate = action.payload
                                                  })
                                                  .addCase(verifyCandidate.rejected, (state, action) => {
                                                    state.status = 'failed'
                                                    state.error = action.error.message
                                                  })
                                                }
                                  })

export const {
                declareCandidate,
                updateCandidateFirstName,
                updateCandidateLastName,
                updateCandidatePartyAbbv,
                updateCandidatePartyName,
                updateCandidateOffice,
                updateCandidateOfficeId,
                updateCandidateOfficeScope,
                updateCandidateOfficeScopeId,
                updateCandidateState,
                updateCandidateStateId,
                updateCandidateLocalGovt,
                updateCandidateLocalGovtId,
                purgeActiveCand
              } = candidatesSlice.actions;

export default candidatesSlice.reducer

export const activeCand = state => state.candidates.candidatedash.activecand
export const lastUpdate = state => state.candidates.candidatedash.lastupdate
