 import { createApi } from '@reduxjs/toolkit/query/react'
import { fetchBaseQuery } from '@reduxjs/toolkit/query'

export const apiSlice = createApi({
    // The cache reducer expects to be added at `state.api` (already default - this is optional)
    reducerPath: 'api',
    // All of our requests will have URLs starting with '/baseurl'
    baseQuery: fetchBaseQuery({ 
                                  baseUrl: `${process.env.ServerURL}`,
                                  prepareHeaders: (headers, { getState }) => {
                                    const token = getState().auth.token
                                    // If we have a token set in state, let's assume that we should be passing it.
                                    if (token) {
                                      headers.set('Authorization', `Bearer ${token}`);
                                    }
                                    return headers
                                  }
                                }),
    tagTypes: ['Pollcount', 'Candcount', 'Party', 'Polls', 'SinglePoll', 'Blogs', 'Blogcount', 'PartyCount', 'Users', 'User'],
    // The "endpoints" represent operations and requests for this server
    endpoints: builder => ({
      getBlogCount: builder.query({
        query: () => '/blogs/blogscount',
        providesTags: ['Blogcount']
        }),
      getBlogList: builder.query({
          query: () => '/blogs/list',
          providesTags: (result=[], error, arg)=> [
            'Blogs',
            ...result.map(({_id})=>({type: 'Blogs', _id}))
          ]
          }),
      getBlog: builder.query({
        query: blogid => `/blog/view/${blogid}`,
        providesTags: (result, error, arg)=> [{type:'Blogs', id: arg._id}]
      }),
      getCandCount: builder.query({
        query: () => '/candidates/candcount',
        providesTags: ['Candcount']
        }),
      getSReps: builder.query({
        query: (sr) => `/${sr.office}/sr/${sr.stateId}`
        }),
      getStates: builder.query({
        query: () => '/states'
        }),
      getPartyList: builder.query({
        query: () => '/parties/list',
        providesTags:['Party']
        }),
      getPartyCount: builder.query({
        query: () => '/partycount',
        providesTags:['PartyCount']
        }),
      getPoll: builder.query({
        query: pollId => `/polls/view/${pollId}`,
        providesTags: (result, error, arg)=> [{type: 'Polls', id: arg}]
        }),
      getPollList: builder.query({
        query: () => ({
          url: '/polls/list',
          responseHandler: (response) => response.json()
        }),
        providesTags: (result=[], error, arg)=> [
          'Polls',
          ...result.map(({id})=> ({type:'Polls', id}))
        ]
        }),
      getPollCount: builder.query({
        query: () => '/polls/pollcount',
        providesTags: ['Pollcount']
        }),
      getRespondentCount: builder.query({
        query: () => '/user/respondentcount'
        }),        
      getRespondentList: builder.query({
          query: () => ({
            url: '/user/respondent/list',
          }),
          providesTags: (result=[], error, arg)=> [
            'Users',
            ...result.map(({id})=> ({type:'Users', id}))
          ]
        }),
      getUserCount: builder.query({
        query: () => '/usercount'
        }),
      getUserList: builder.query({
        query: () => ({
          url: '/user/list',
        }),
        providesTags: (result=[], error, arg)=> [
          'Users',
          ...result.map(({id})=> ({type:'Users', id}))
        ]
      }),
      getUserProfile: builder.query({
        query: uid => `/user/profile/${uid}`,
        providesTags: (result, error, arg) => [{type: 'Users', id:arg}]
      }),
      addPollCand:builder.mutation({
        query: candData => ({
            url: '/polls/addcandidate',
            method: 'POST',
            headers: {
                'Content-type': 'application/x-www-form-urlencoded'
                },
          body: new URLSearchParams(candData).toString()
        }),
        invalidatesTags: (result, error, arg)=>[{type: 'Polls', id: arg._id}]
        }),
      approveBlog: builder.mutation({
        query: blogid => ({
          url: "/blogs/approve",
          method: 'POST',
          headers: {
                    "Content-type":"application/x-www-form-urlencoded"
                    },
          body: new URLSearchParams({blogid})
        }),
        invalidatesTags:(result, error, arg)=>[{type: 'Blogs', id: arg}]
        }),
      approveRespondent: builder.mutation({
          query: userid => ({
            url: "/user/respondent/approve",
            method: 'POST',
            headers: {
                      "Content-type":"application/x-www-form-urlencoded"
                      },
            body: new URLSearchParams({userid})
          }),
          invalidatesTags:(result, error, arg)=>[{type: 'Users', id: arg}]
          }),
      createPoll: builder.mutation({
        query: pollData => ({
          url: '/polls/create/wocand',
          method: 'POST',
          headers: {
                'Content-type': 'application/x-www-form-urlencoded'
          },
          body: new URLSearchParams(pollData).toString()
        }),
        invalidatesTags:['Pollcount', 'Polls']
        }),
      deleteParty: builder.mutation({
        query: partyData => ({
          url: '/party/delete',
          method: 'POST',
          headers: {
                      'Content-type': 'application/x-www-form-urlencoded'
                  },
          body: new URLSearchParams(partyData).toString()
        }),
        invalidatesTags:['Party', 'PartyCount']
      }),
      suspendBlog: builder.mutation({
          query: blogid => ({
            url: "/blogs/suspend",
            method: 'POST',
            headers: {
                      "Content-type":"application/x-www-form-urlencoded"
                      },
            body: new URLSearchParams({blogid})
          }),
          invalidatesTags:(result, error, arg)=>[{type: 'Blogs', id: arg}]
        }),
      suspendRespondent: builder.mutation({
          query: blogid => ({
            url: "/user/respondent/suspend",
            method: 'POST',
            headers: {
                      "Content-type":"application/x-www-form-urlencoded"
                      },
            body: new URLSearchParams({userid})
          }),
          invalidatesTags:(result, error, arg)=>[{type: 'Users', id: arg}]
        })      
    })
  })

  export const { 
    useGetBlogQuery,
    useGetBlogCountQuery,
    useGetBlogListQuery,
    useGetCandCountQuery,
    useGetPartyListQuery,
    useGetPartyCountQuery,
    useGetPollCountQuery,
    useGetPollListQuery,
    useGetPollQuery,
    useGetRespondentCountQuery,
    useGetRespondentListQuery, 
    useGetSRepsQuery,
    useGetStatesQuery,
    useGetUserCountQuery,
    useGetUserListQuery,
    useGetUserProfileQuery,
    useAddPollCandMutation,
    useApproveBlogMutation,
    useApproveRespondentMutation,
    useCreatePollMutation,
    useDeletePartyMutation,
    useSuspendBlogMutation,
    useSuspendRespondentMutation
  } = apiSlice