import React, {useEffect, useState} from 'react'
import { useDispatch } from 'react-redux'
import { Link, useParams } from 'react-router-dom'
import { useGetPollQuery, useAddPollCandMutation, apiSlice } from '../api/apislice'
import { Spinners } from '../../app/spinner'


export const PollView = () => {
  const { pollId } = useParams()
  const { officeScope } = useParams()
  const {data: poll, isFetching, isSuccess } = useGetPollQuery(pollId)
  const [ hidden, setHidden ] = useState('hidden')
  const [ span, setSpan ] = useState('hidden')
  const [ candidate, setCandidate ] = useState({pollId})
  const [ inputValue, setInputValue ] = useState('')
  const [ parties, setParties ] = useState([])
  const [ psdParties, setPsdParties ] = useState([])
  const dispatch = useDispatch()
  const [ addPollCand ] = useAddPollCandMutation()
  const showAddCand = () => {
        setHidden('')
        if(!parties.length>0){
        dispatch(apiSlice.endpoints.getPartyList.initiate())
        .then(res => {setParties(res.data)})
        }
    }
  const add = () => {
    console.log(candidate)
    const canSave = ([candidate.firstName,
                      candidate.lastName,
                      candidate.partyName,
                      candidate.partyAbbv
                      ]).every(Boolean)
    if(canSave){
      addPollCand(candidate)
      setCandidate({pollId})
      setInputValue('')
    }
  }
  const onSelectParty = e => {
                                if(e.key ==="Enter"|| undefined){
                                setInputValue(e.target.dataset.abbv.toUpperCase())
                                setPsdParties([])
                                setSpan('hidden')
                                if(parties.some(({abbv})=> abbv === e.target.dataset.abbv) ){
                                    const PartyName = e.target.dataset.name
                                    const PartyAbbv = e.target.dataset.abbv.toUpperCase()
                                    setCandidate(values => ({...values, ['partyName']: PartyName}))
                                    setCandidate(values => ({...values, ['partyAbbv']: PartyAbbv}))

                                }}
                              }
  const handleChange = (e) => {
                              const name = e.target.name
                              const value = e.target.value 
                              if(name !== 'partyName'){
                                setCandidate(values => ({...values, [name]: value}))
                              }else{
                                if(e.target.value.length > 0){
                                  setInputValue(e.target.value.toUpperCase()) 
                                  setSpan('')
                                  const regex = new RegExp(`^${e.target.value}`, 'i')
                                  const orderedParties = parties.slice().sort((a,b)=>{
                                                                                        if (a.abbv < b.abbv){
                                                                                          return -1
                                                                                        }
                                                                                        if (a.abbv > b.abbv){
                                                                                          return 1
                                                                                        }
                                                                                        return 0
                                                                                        })
                                  const foundParties = orderedParties.filter(({abbv}) => abbv.match(regex))
                                  setPsdParties(foundParties)
                                  if(parties.some(({abbv})=> abbv === e.target.value.toUpperCase()) && foundParties.length === 1){
                                    const PartyName = (foundParties[0].name)
                                    const PartyAbbv = (foundParties[0].abbv)
                                    setCandidate(values => ({...values, ['partyName']: PartyName}))
                                    setCandidate(values => ({...values, ['partyAbbv']: PartyAbbv}))
                                    setPsdParties([])
                                  }
                                }else{
                                  setInputValue('')
                                  setPsdParties([])
                                  setSpan('hidden')
                                }
                              }
                              
                        }
  const parsedParties = psdParties.map((prty, i) =>(
    <span 
      className="parsedobjects-items" 
      tabIndex={0}
      key={prty._id} 
      data-id={prty._id} 
      data-abbv={prty.abbv} 
      data-name={prty.name} 
      type="button"
      onKeyDown={onSelectParty}
      onClick = {onSelectParty}
    >
      {`${prty.abbv} - ${prty.name}`}
    </span>
  ))
  
  const cancelAdd = () => {
      setParties([])
      setHidden('hidden')
  }
  let content
  if(isFetching){
    content = <Spinners/>
  }else if (isSuccess){
    content = poll.candidates.map((candidate, i) => (
                                                <div className="row mb-2" key={candidate._id}>
                                                  <div className="col-sm-1 text-capitalize">
                                                    <input
                                                      className="form-check-input"
                                                      type="checkbox"
                                                      id={candidate._id}
                                                      value={candidate._id}
                                                      name={candidate._id}
                                                    />
                                                  </div>
                                                  <div className="col-sm-1 text-capitalize">
                                                    {i+=1}
                                                  </div>
                                                  <div className="col-sm-3 text-capitalize">
                                                  {candidate.firstName} {candidate.lastName}
                                                  </div>
                                                  <div className="col-sm-3 text-capitalize">
                                                  {candidate.partyName}
                                                  </div>
                                                  <div className="col-sm-1 text-capitalize">
                                                  {candidate.partyAbbv}
                                                  </div>
                                                  <div className="col-sm-2">
                                                    <Link to={`/candidate/remove/${candidate._id}`}>
                                                      <div
                                                      className='btn btn-danger btn-sm w-100'
                                                      >
                                                        Remove
                                                      </div>
                                                    </Link>
                                                  </div>
                                                </div>
                                                ))         
                       }

  return(
            <section className="poll-list">
              <div className="leadtitle">
              <h1>{officeScope} Poll</h1>
              </div>
              <div className="dashboard-block">
                <div className="container">
                  <div className="row mb-5">
                    <div className="col-sm-4">
                      <div 
                        className="btn btn-success"
                        onClick={showAddCand}
                        tabIndex={0}
                        onKeyDown={showAddCand}
                        >Add Candidate</div>
                    </div>
                    <div className="col-sm-4">
                      <input
                        className="form-control mx-2"
                        placeholder="Search Polls"
                        
                      />
                    </div>
                    <div className="col-sm-4">
                      <Link to = "/polls/dashboard">
                      <div 
                        className="btn btn-info"
                        
                        >Go to Dashboard</div>
                      </Link>
                    </div>
                  </div>
                  {content}
                    <div 
                      className="row no-gutters my-5" 
                      hidden={hidden}
                      >
                      <div className="d-flex col-sm-2 align-items-center">
                      <input 
                        className="form-control mx-1"
                        placeholder="Surname"
                        autocomplete="off"
                        name="lastName"
                        value={candidate['lastName'] || ''}
                        onChange={handleChange}
                      />
                      </div>
                      <div className="d-flex col-sm-2 align-items-center">
                        <input 
                          className="form-control mx-1"
                          placeholder="firstname"
                          autocomplete="off"
                          name="firstName"
                          value={candidate['firstName'] || ''}
                          onChange={handleChange}
                        />
                      </div>
                      <div className="d-flex col-sm-2 align-items-center">
                      <div className="d-flex flex-column">
                        <input 
                          className="form-control mx-1"
                          placeholder="party name"
                          autocomplete="off"
                          name="partyName"
                          value={inputValue}
                          onChange={handleChange}
                        />
                      </div>
                      
                      </div>                    
                      <div className="d-flex col-sm-2 justify-content-center">
                        <button
                            className='btn btn-success btn-sm mx-2 w-100'
                            onClick={add}
                            >Add
                        </button>
                      </div>
                      <div className="d-flex col-sm-2 justify-content-center">
                        <button
                          className='btn btn-info btn-sm mx-2 w-100'
                          onClick={cancelAdd}
                          >Cancel</button>
                      </div>
                    </div>
                    <div 
                      className="row no-gutters justify-content-center mt-1" 
                      id="pollview-partylist"
                      hidden={hidden}
                      >
                      <div className="parsedobjects" hidden={span}>{parsedParties}</div>
                    </div>
                </div>
              </div>
            </section>
        )
}

