import { useParams } from 'react-router-dom'
import { useGetBlogQuery, useApproveBlogMutation, useSuspendBlogMutation } from '../api/apislice'
import { Spinners } from '../../app/spinner'
import escapeHtml from 'escape-html'
import { Text } from 'slate'
import { Link } from 'react-router-dom'

export const BlogView = () => {
    const { blogid } = useParams()
    const {
        data: blog = {},
        isLoading,
        isSuccess,
        isError,
        error
    } = useGetBlogQuery(blogid)
    const serialize = node => {
        if (Text.isText(node)) {
          let string = escapeHtml(node.text)
          if (node.bold) {
            string = `<strong>${string}</strong>`
          }
          if(node.italic){
            string = `<em>${string}</em>`
          }
          if(node.code){
              string = `<code>${string}</code>`
          }
          return string
        }
      
        const children = node.children.map(n => serialize(n)).join('')
      
        switch (node.type) {
          case 'block-quote':
            return `<blockquote><p>${children}</p></blockquote>`
          case 'paragraph':
            return `<p>${children}</p>`
          case 'link':
            return `<a href="${escapeHtml(node.url)}">${children}</a>`
          case 'heading-one':
              return `<h1>${children}</h1>`
          case 'heading-two':
              return `<h2>${children}</h2>`
          case 'numbered-list':
              return `<ol>${children}</ol>`
          case 'bulleted-list':
              return `<ul>${children}</ul>`
          case 'list-item':
              return `<li>${children}</li>`
          default:
            return children
        }
      }
     const SlateContent = () => {
          return(
              <div dangerouslySetInnerHTML={{__html: serialize({children:blog.body})}} />
                  
                  )
     }
    const [approveBlog] = useApproveBlogMutation()
    const [suspendBlog] = useSuspendBlogMutation()
    const blogApprove = () => {
                                approveBlog(blog._id)
                            }
    const blogSuspend = () => {
                                suspendBlog(blog._id)
                            }
   let content
   if(isLoading){
    content = (<div className="submitblogspinner text-success"><Spinners/></div>)
   }else if(isSuccess){
    content = ( 
                <div className="d-flex flex-column justify-content-center w-50">
                <img src={blog.image} className="img-fluid"/>
                <div className="mb-5">
                <h1 className=" display-3 text-capitalize text-success">{blog.title}</h1>
                <footer className="blockquote-footer">{blog.publisher} <cite title="Source Title">{(new Date(blog.date)).toUTCString()}</cite></footer>
                </div>
                
                
                <div className="blog-header mb-2">{blog.header}</div>
                <div className="blog-paragraph"><SlateContent/></div>
                <div className="d-flex flex-row align-self-center my-5 w-50">
                    {blog.approved ?
                    <div className="col-sm-6">
                        <button 
                            className="btn btn-danger w-100"
                            onClick={blogSuspend}
                            >Suspend
                        </button>
                    </div>
                    :
                    <div className="col-sm-6">
                        <button 
                            className="btn btn-success w-100"
                            onClick={blogApprove}
                            >Approve
                        </button>
                    </div>
                    }
                   <div className="col-sm-6">
                      <Link to = "/blogs/dashboard">
                      <div 
                        className="btn btn-info w-100"
                        
                        >Go to Dashboard</div>
                      </Link>
                    </div>
                </div>
                </div>
                )
   }
    return(
        <section className="poll-list" id="newblog">
        {content}
        </section>
    
    )
}