import React, { useState, useEffect, useSelector } from 'react'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { loggedAdmin } from '../users/usersSlice'
import { useGetPartyListQuery, useDeletePartyMutation } from '../api/apislice'




export const PartyDash = () => {
  const {
          data: partylist = [], 
          isLoading, 
          isSuccess, 
          isError,
          err
        } = useGetPartyListQuery()
  const [deleteParty]= useDeletePartyMutation()
  const admin = useSelector(loggedAdmin)
  const [inputValue, setInputValue] = useState('')
  const [selectedParty, setSelectedParty] = useState('')
  const [PartyId, setPartyId] = useState('')
  const [parties, setParties] = useState([])
  const [psdParties, setPsdParties] = useState([])
  const [error, setError] = useState({})
  const [lastUpdate, setLastUpdate] = useState('none')
  const onPartiesrc = e => {
                          if(e.target.value.length > 0){
                                                      setInputValue(e.target.value)
                                                      const regex = new RegExp(`^${e.target.value}`, 'i')
                                                      const foundPTs = parties.filter(({name}) => name.match(regex))
                                                      setPsdParties(foundPTs)
                                                }else{
                                                      setPsdParties(parties)
                                                      setInputValue('')
                                                    }

                          }
  const onPartieselect = e =>{
                              console.log(e.target.value)
                            }

  const vpack = new Object()
        vpack.actionId = admin.uid
        vpack.actionFname = admin.firstName
        vpack.actionLname = admin.lastName
  const onDelete = e => {
                          vpack.partyId = e.target.dataset.id
                          deleteParty(vpack)
                        }
  useEffect(()=>{           
                      if(isSuccess){
                        setParties(partylist)
                        setPsdParties(partylist)
                      }
                   })
  const orderedParty = psdParties.slice().sort((a,b)=>{
                                              if (a.name < b.name){
                                                return -1
                                              }
                                              if (a.name > b.name){
                                                return 1
                                              }
                                              return 0
                                            })
  const displayParty = orderedParty.map((Party, i)=>(
                                              <div className="row mb-2" key={Party._id}>
                                                  <div className="col-sm-1 text-capitalize">
                                                    <input
                                                      className="form-check-input"
                                                      type="checkbox"
                                                      id={Party._id}
                                                      value={Party._id}
                                                      name={`${Party.name}`}
                                                      onChange={onPartieselect}
                                                    />
                                                  </div>
                                                  <div className="col-sm-1 text-capitalize">
                                                    {i+=1}
                                                  </div>
                                                  <div className="col-sm-4 text-capitalize">
                                                    {Party.name}
                                                  </div>
                                                  <div className="col-sm-2 text-capitalize">
                                                    {Party.party} 
                                                  </div>
                                                  <div className="col-sm-2 text-capitalize">
                                                    {Party.abbv}
                                                  </div>
                                                  <div className="col-sm-2">
                                                    <button
                                                      className='btn btn-danger btn-sm w-100'
                                                      data-id={Party._id}
                                                      onClick={onDelete}
                                                      >
                                                        Delete 
                                                    </button>
                                                  </div>
                                              </div>
                                            ))
  return (
            <section className="poll-list">
              <div className="leadtitle">
                <h1>parties Board</h1>
              </div>
              <div className="dashboard-block">
                <div className="container">
                  <div className="row mb-5">
                    <div className="col-sm-4">
                      <Link to="/party/create"><div className="btn btn-success">New Party</div></Link>
                    </div>
                    <div className="col-sm-4">
                      <input
                        className="form-control mx-2"
                        placeholder="Search Party"
                        onChange={onPartiesrc}
                        value={inputValue}
                      />
                    </div>
                    <div className="col-sm-4">
                      <Link to = "/dashboard">
                      <div 
                        className="btn btn-info"
                        
                        >Go to Dashboard</div>
                      </Link>
                    </div>
                  </div>
                  {displayParty}
                </div>
              </div>
            </section>
          )
}
