import React, { useState, useEffect } from 'react'
import { useDispatch } from 'react-redux'
import { v4 as uuidv4 } from 'uuid';
import { declareCandidate, updateCandidateFirstName } from './candidatesSlice'

export const FirstName = () =>{
  const [firstName, setFirstName] = useState('')
  const [inputValue, setInputValue] = useState('')
  const dispatch = useDispatch()
  const onNameChange = e => {
                                setInputValue(e.target.value.toLowerCase())
                                setFirstName(e.target.value.toLowerCase())
                              }
  const onSubmitName = () =>{
    dispatch(declareCandidate(uuidv4()))
    dispatch(updateCandidateFirstName(firstName));
  }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
  })
  return(
    <div
      id="form-body"
      className="form-body">
      <h1 className="form-header">What is candidate's first name?</h1>
      <div className="form-section">
      <input
        className="form-control mx-2"
        onChange={onNameChange}
        value={inputValue}
      />
      <button
        type="button"
        className="btn btn-success mx-2"
        onClick = {onSubmitName}
        >
        Submit
        </button>
       </div>
    </div>
  )
}
