import React, { useState, useEffect } from 'react'
import { useDispatch } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { useGetStatesQuery, useCreatePollMutation, apiSlice } from '../api/apislice'




export const PollsCreate = () => {
  const {data: states, isFetching, isSuccess } = useGetStatesQuery()
  const [office, setOffice] = useState('')
  const [officeId, setOfficeId] = useState('')
  const [officeScopeId, setOfficeScopeId] = useState('')
  const [officeScope, setOfficeScope] = useState('')
  const [placeholder, setPlaceholder] = useState('')
  const [placeholder2, setPlaceholder2] = useState('')
  const [disabled, setDisabled] = useState('')
  const [span, setSpan] = useState('hidden')
  const [span2, setSpan2] = useState('hidden')
  const [inputVis, setInputVis] = useState('hidden')
  const [inputVis2, setInputVis2] = useState('hidden')
  const [buttonVis, setButtonVis] = useState('hidden')
  const [inputValue2, setInputValue2] = useState('')
  const [inputValue, setInputValue] = useState('')
  const [cstts, setCstts] = useState([])
  const [psdCstts, setPsdCstts] = useState([])
  const [createPoll, {isLoading}] = useCreatePollMutation()
  const dispatch = useDispatch()
  const navigate = useNavigate()
  
  useEffect(()=>{       
      
                  })
    const onCsttChange = e => {
                    if(e.target.value.length > 0){
                                                setInputValue(e.target.value)
                                                setSpan('')
                                                const regex = new RegExp(`^${e.target.value}`, 'i')
                                                const foundCstts = cstts.filter(({name}) => name.match(regex))
                                                setPsdCstts(foundCstts)
                                                if(cstts.some(({name})=> name.toLowerCase() === e.target.value.toLowerCase())){
                                                  //setStateId(foundCstts[0]._id)
                                                  //setState(e.target.value.toLowerCase())
                                                  //setPsdCstts([])
                                                }else{
                                                  setOfficeScope('')
                                                }
                                              }else{
                                                setInputValue('')
                                                setPsdCstts([])
                                                setSpan('hidden')
                                              }
                } 
    const onCsttChange2 = e => {
      if(e.target.value.length > 0){
                                  setInputValue2(e.target.value)
                                  setSpan2('')
                                  const regex = new RegExp(`^${e.target.value}`, 'i')
                                  const foundCstts = cstts.filter(({name}) => name.match(regex))
                                  setPsdCstts(foundCstts)
                                  if(cstts.some(({name})=> name.toLowerCase() === e.target.value.toLowerCase())){
                                    setOfficeScopeId(foundCstts[0]._id)
                                    setOfficeScope(e.target.value.toLowerCase())
                                    setPsdCstts([])
                                  }else{
                                    setOfficeScope('')
                                  }
                                }else{
                                  setInputValue2('')
                                  setPsdCstts([])
                                  setSpan2('hidden')
                                }
      } 
    const onSelectCstt = e => {
                  
                  setInputValue(e.target.innerHTML)
                  setCstts([])
                  setPsdCstts([])
                  setSpan('hidden')
                  if(cstts.some(({name})=> name === e.target.innerHTML) ){
                    setDisabled('disabled')
                    dispatch(apiSlice.endpoints.getSReps.initiate({office, stateId:e.target.dataset.id}))
                    .then(res => {setCstts(res.data)})
                    setInputVis2('')
                  }
                }
    const onSelectCstt2 = e => {
                  setInputValue2(e.target.innerHTML)
                  setPsdCstts([])
                  setSpan2('hidden')
                  if(cstts.some(({name})=> name === e.target.innerHTML) ){
                    setOfficeScopeId(e.target.dataset.id)
                    setOfficeScope(e.target.innerHTML.toLowerCase())
                    setButtonVis('')
                  }
                }
    const onInputChange = e => {
                    setInputValue('')
                    setInputVis('hidden')
                    setInputVis2('hidden')
                    setButtonVis('hidden')
                    setSpan('hidden')
                    setSpan2('hidden')
                    setOffice(e.target.value)
                    setOfficeId(e.target.dataset.officeid)
                    setDisabled('')
                    if(e.target.value !== 'presidential'){ 
                      setInputVis('')
                      setPlaceholder('Enter State')
                      setPlaceholder2(e.target.dataset.pl)
                      if(isSuccess){
                          setCstts(states)
                        }
                    }else{
                            setOfficeScopeId('x06PR0112plx06PR0112pl')
                            setOfficeScope('presidential')
                            setInputVis('hidden')
                            setInputVis2('hidden')
                            setButtonVis('')
                    }
                  }
    const parsedCstts = psdCstts.map((cstt, i) =>(
                    <span className="parsedobjects-items"
                      tabIndex={0} 
                      key={cstt._id} 
                      data-id={cstt._id} 
                      type="button"
                      onKeyDown={onSelectCstt}
                      onClick = {onSelectCstt}
                    >
                      {cstt.name}
                    </span>
                  ))
    const parsedCstts2 = psdCstts.map((cstt, i) =>(
                    <span className="parsedobjects-items" 
                      tabIndex={0}
                      key={cstt._id} 
                      data-id={cstt._id} 
                      type="button"
                      onKeyDown={onSelectCstt2}
                      onClick = {onSelectCstt2}
                    >
                      {cstt.name}
                    </span>
                  ))
    
    
    const onSubmitOffice = async ()=>{
      const poll = new Object()
        poll.officeId = officeId
        poll.office = office
        poll.officeScopeId = officeScopeId
        poll.officeScope = officeScope
        const canSave = ([officeId, office, officeScopeId, officeScope]).every(Boolean) && !isLoading
        if(canSave){
            try{
                  await createPoll(poll)
                  .then(res => { 
                                    console.log(res.data)
                                    let resid = res.data.id
                                    let resofficescope = res.data.officeScope
                                    navigate(`/polls/view/${resid}/${resofficescope}`)
                                  })
            }catch(error){

            }
        }
    }
  
  return (
            <section className="poll-list">
              <div className="leadtitle">
                <h1>New Poll</h1>
              </div>
              <div className="form-section">
                <div className="container-fluid">
                <div className="col-sm-12">
                <div className="custom-control custom-radio custom-control-inline mx-2">
                  <input
                    type="radio"
                    id="presidential"
                    data-officeid="x06PR0112pl"
                    data-pl="country"
                    name="office"
                    value="presidential"
                    className="custom-control-input"
                    onChange ={onInputChange}
                    />
                  <label className="custom-control-label" htmlFor="presidential">Presidential</label>
                </div>
                <div className="custom-control custom-radio custom-control-inline mx-2">
                  <input
                    type="radio"
                    id="senate"
                    data-officeid="x05SE0306se"
                    data-pl="federal constituency"
                    name="office"
                    value="senate"
                    className="custom-control-input"
                    onChange ={onInputChange}
                    />
                  <label className="custom-control-label" htmlFor="senate">Senate</label>
                </div>
                <div className="custom-control custom-radio custom-control-inline mx-2">
                  <input
                    type="radio"
                    id="houseOfRep"
                    data-officeid="x04HRxx10hp"
                    data-pl="federal constituency"
                    name="office"
                    value="houseOfRep"
                    className="custom-control-input"
                    onChange ={onInputChange}
                    />
                  <label className="custom-control-label" htmlFor="houseOfRep">House of Rep</label>
                </div>
                <div className="custom-control custom-radio custom-control-inline mx-2">
                  <input
                    type="radio"
                    id="governorship"
                    data-officeid="x03GO0112gp"
                    data-pl="state"
                    name="office"
                    value="governorship"
                    className="custom-control-input"
                    onChange ={onInputChange}
                    />
                  <label className="custom-control-label" htmlFor="governorship">Governorship</label>
                </div>
                <div className="custom-control custom-radio custom-control-inline mx-2">
                  <input
                    type="radio"
                    id="stateRep"
                    data-officeid="x02SRxx08sp"
                    data-pl="state constituency"
                    name="office"
                    value="stateRep"
                    className="custom-control-input"
                    onChange ={onInputChange}
                    />
                  <label className="custom-control-label" htmlFor="stateRep">State Rep</label>
                </div>
                <div className="custom-control custom-radio custom-control-inline mx-2">
                  <input
                    type="radio"
                    id="localGovt"
                    data-officeid="x01LGxx09lt"
                    data-pl="local government"
                    name="office"
                    value="localGovt"
                    className="custom-control-input"
                    onChange ={onInputChange}
                    />
                  <label className="custom-control-label" htmlFor="localGovt">Local Govt</label>
                </div>
                </div>
                </div>
              </div>
              <div className="form-section">
              <input
                className="form-control mx-2"
                onChange={onCsttChange}
                placeholder={placeholder}
                disabled={disabled}
                hidden={inputVis}
                value = {inputValue}
              />
              </div>
              <div className="form-section">
              <input
                className="form-control mx-2"
                onChange={onCsttChange2}
                placeholder={placeholder2}
                hidden={inputVis2}
                value = {inputValue2}
              />
              </div>
              <div className="form-section">
                <button
                  type="button"
                  className="btn btn-success"
                  hidden={buttonVis}
                  onClick = {onSubmitOffice}
                  >Submit</button>
                </div>
              <div className="parsedobjects" hidden={span}>{parsedCstts}</div>
              <div className="parsedobjects" hidden={span2}>{parsedCstts2}</div>
            </section>
          )
}
