import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useGetPollListQuery } from '../api/apislice'
import { Spinners } from '../../app/spinner'


export const PollsDash = () => {
  const {
          data: polllist = [], 
          isLoading, 
          isSuccess, 
          isError,
          error
        } = useGetPollListQuery()
  const [polls, setPolls] = useState([])
  const [psdPolls, setPsdPolls] = useState([])
  const [inputValue, setInputValue] = useState('')
  useEffect(()=>{
    if(isSuccess){
                    setPsdPolls(polllist)
                    setPolls(polllist)
                  }
    else if(isError){
      console.log(`${error.status}: ${JSON.stringify(error.data)}`)
    }
  })  
  const onPollselect = e =>{
                              console.log(e.target.value)
                            }
  const onPollSrc = e => {
    
    if(e.target.value.length > 0){
                                    setInputValue(e.target.value)
                                    const regex = new RegExp(`^${e.target.value}`, 'i')
                                    const foundCIs = polllist.filter(({officeScope}) => officeScope.match(regex))
                                    setPsdPolls(foundCIs)
                                }else{
                                    setPsdPolls(polllist)
                                    setInputValue('')
                                }

    }


  
    const content = psdPolls.slice().sort((a,b)=>{
                                                if (a.officeScope < b.officeScope){
                                                  return -1
                                                }
                                                if (a.officeScope > b.officeScope){
                                                  return 1
                                                }
                                                  return 0
                                              }).map((poll, i)=>(
                                                <div className="row mb-2" key={poll._id}>
                                                    <div className="col-sm-1 text-capitalize">
                                                      <input
                                                        className="form-check-input"
                                                        type="checkbox"
                                                        id={poll._id}
                                                        value={poll._id}
                                                        name={poll.office}
                                                        onChange={onPollselect}
                                                      />
                                                    </div>
                                                    <div className="col-sm-1 text-capitalize">
                                                      {i+=1}
                                                    </div>
                                                    <div className="col-sm-4 text-capitalize">
                                                      {poll.office}
                                                    </div>
                                                    <div className="col-sm-2 text-capitalize">
                                                      {poll.officeScope} 
                                                    </div>
                                                    <div className="col-sm-2 text-capitalize">
                                                      {poll.Active ? 'Active': 'Inactive'}
                                                    </div>
                                                    <div className="col-sm-2">
                                                      <Link to={`/polls/view/${poll._id}/${poll.officeScope}`}>
                                                        <div
                                                        className='btn btn-info btn-sm w-100'
                                                        >
                                                          View
                                                        </div>
                                                      </Link>
                                                    </div>
                                                </div>
                                                )
                                              )

  return (
            <section className="poll-list">
              <div className="leadtitle">
                <h1>Polls Board</h1>
              </div>
              <div className="dashboard-block">
                <div className="container">
                  <div className="row mb-5">
                    <div className="col-sm-4">
                      <Link to="/polls/create"><div className="btn btn-success">New Poll</div></Link>
                    </div>
                    <div className="col-sm-4">
                      <input
                        className="form-control mx-2"
                        placeholder="Search Polls"
                        onChange={onPollSrc}
                        value={inputValue}
                      />
                    </div>
                    <div className="col-sm-4">
                      <Link to = "/dashboard">
                      <div 
                        className="btn btn-info"
                        
                        >Go to Dashboard</div>
                      </Link>
                    </div>
                  </div>
                  {content}
                </div>
              </div>
            </section>
          )
}
