import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { updateCandidate, activeCand } from './candidatesSlice'

export const Attestation = () =>{
  const [buttonVis, setButtonVis] = useState('hidden')
  const [proceedBtnVis, setProceedBtnVis] = useState('hidden')
  const [updateCandidateStatus, setUpdateCandidateStatus] = useState('idle')
  const candidate = useSelector(activeCand)
  const dispatch = useDispatch()
  const onSelectYes = e =>  {setButtonVis('')}
  const onSelectNo = e =>  {setButtonVis('hidden')}
  const canSubmit = (updateCandidateStatus === 'idle')
  const onSubmitProfile = async () => {
                                        if(canSubmit){
                                                      try{
                                                          setUpdateCandidateStatus('pending')
                                                          await dispatch(updateCandidate(candidate)).unwrap()
                                                            setButtonVis('hidden')
                                                            setProceedBtnVis('')
                                                          }catch(err){
                                                            console.error('Failed to save the post: ', err)
                                                          }finally{
                                                            setUpdateCandidateStatus('idle')
                                                          }
                                                        }
                                        }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
  })
  return(
      <div
      id="form-body"
      className="form-body">
      <h1 className="form-header text-success">Attestation</h1>
      <div className="form-section">
          <div className="form-check mx-3">
            <input
              className="form-check-input"
              type="radio"
              id="doAttest"
              value="attest"
              name="attestation"
              onChange={onSelectYes}
            />
            <label className="form-check-label" htmlFor="doAttest">I attest</label>
          </div>
          <div className="form-check mx-3">
          <input
            className="form-check-input"
            type="radio"
            id="doNotAttest"
            name="attestation"
            value="notAttest"
            onChange={onSelectNo}
          />
          <label className="form-check-label" htmlFor="doNotAttest">I do not attest</label>
          </div>
      </div>
      <div className="form-section">
        <p>I attest that the information provided are accurate and reflect the true scenario</p>
      </div>
      <div className="form-section">
      <div>
      <button
        type="button"
        className="btn btn-success mx-2"
        hidden = {buttonVis}
        onClick = {onSubmitProfile}
        disabled = {!canSubmit}
        >
        Submit
        </button>
        </div>
      <div>
        <Link
          to="/candidate/dashboard"
          type="button"
          className="btn btn-success mx-2"
          hidden = {proceedBtnVis}
          >
          Proceed
          </Link>
        </div>
      </div>
    </div>
  )
}
