import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import { useGetBlogCountQuery } from '../api/apislice'


export const Blogs = () => {
  const [blogCount, setBlogCount] = useState(0)
  const {
    data: blogs,
    isLoading,
    isSuccess,
    isError,
    error
} = useGetBlogCountQuery()

useEffect(()=>{
    if(isSuccess){
                    setBlogCount(blogs.count)
    }
})

return(
          <div className="poll-list">
            <div>
              <h4>All Blogs</h4>
            </div>
            <div className="dash-numbers">
            <Link to="/blogs/dashboard"><h2>{blogCount}</h2></Link>
            </div>
          </div>
        )
}