import { useEffect } from 'react';
import { useGetUserProfileQuery, useApproveRespondentMutation, useSuspendRespondentMutation } from '../api/apislice';
import { useParams } from 'react-router-dom'

export const UserProfile = () => {
  const [approveRespondent] = useApproveRespondentMutation()
  const [suspendRespondent] = useSuspendRespondentMutation()

  const { userid } = useParams()
  const {
    data: user = [], 
    isLoading, 
    isSuccess, 
    isError,
    error
  } = useGetUserProfileQuery(userid) 
  const onApprove = () => {
      approveRespondent(userid)
  }
  const onSuspend = () => {
      suspendRespondent(userid)
  }
  useEffect(()=>{
      
  })
                          
  return (
  <section className="poll-list" id="profile">
    <div className="leadtitle">
      <h1>{`ID: ${user._id} `}</h1>
    </div>
    <div className="presidential-block mt-5 w-50">
      <article className="poll-excerpt w-100">
        <div className="container w-100"><h6 className="float-left">Firstname:</h6><span className="profile-list float-right">{user.firstName}</span></div>
        <div className="container w-100"><h6 className="float-left">Lastname:</h6><span className="profile-list float-right">{user.lastName}</span></div>
    {user.sex ? <div className="container w-100"><h6 className="float-left">Sex: </h6><span className="profile-list float-right">{user.sex}</span></div>: null }
    {user.age ? <div className="container w-100"><h6 className="float-left">Age: </h6><span className="profile-list float-right">{user.age}</span></div>: null }
    {user.phone ? <div className="container w-100"><h6 className="float-left">Phone: </h6><span className="profile-list float-right">{user.phone}</span></div>: null }
        <div className="container w-100"><h6 className="float-left">Email: </h6><span className="profile-list float-right">{user.email}</span></div>
        <div className="container w-100"><h6 className="float-left">Country: </h6><span className="profile-list float-right">{user.country}</span></div>
        <div className="container w-100"><h6 className="float-left">State: </h6><span className="profile-list float-right">{user.state}</span></div>
        <div className="container w-100"><h6 className="float-left">Local Govt: </h6><span className="profile-list float-right">{user.localGovt}</span></div>
        <div className="container w-100"><h6 className="float-left">State Constituent: </h6><span className="profile-list float-right">{user.constituent}</span></div>
    {user.socialprofile ?  <div className="container w-100"><h6 className="float-left">Social Profile: </h6><span className="profile-list float-right">{user.socialprofile}</span></div> : null }
    {user.videolink ?  <div className="container w-100"><h6 className="float-left">Video Link: </h6><span className="profile-list float-right">{user.videolink}</span></div> : null }
 
      </article>
    </div>
    {
      user.profilecomplete? <div className="container-fluid">
      <div className="d-flex justify-content-center mt-5">
        {user.respondent === "requested" ? <button className="rounded-0 btn btn-success text-light" onClick={onApprove}>Approve Respondent</button> : null}
        {user.respondent === "approved" ? <button className="rounded-0 btn btn-danger text-light" onClick={onSuspend}>Suspend Respondent</button> : null}
      </div>
      </div>: null
    }
  </section>
  );
};