import React, { useState, useEffect, useSelector } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { activeCand, updateCandidateLocalGovt, updateCandidateLocalGovtId} from './candidatesSlice'
import { selectToken } from '../auth/authSlice'

export const LocalGovt = () =>{
  const candidate = useSelector(activeCand)
  const authToken = useSelector(selectToken)
  const [localGovt, setLocalGovt] = useState('')
  const [localGovtId, setLocalGovtId] = useState('hidden')
  const [span, setSpan] = useState('hidden')
  const [lgs, setLgs] =  useState([])
  const [psdLgs, setPsdLgs] = useState([])
  const [inputValue, setInputValue] = useState('')
  const [error, setError] = useState({})
  const dispatch = useDispatch()
  const onLGChange = e => {
    if(e.target.value.length > 0){
                                setInputValue(e.target.value)
                                setSpan('')
                                const regex = new RegExp(`^${e.target.value}`, 'i')
                                const foundLgs = lgs.filter(({name}) => name.match(regex))
                                setPsdLgs(foundLgs)
                                if(lgs.some(({name})=> name.toLowerCase() === e.target.value.toLowerCase())){
                                  setLocalGovtId(foundLgs[0]._id)
                                  setLocalGovt(e.target.value.toLowerCase())
                                  setPsdLgs([])
                                }else{
                                  setLocalGovt('')
                                }
                              }else{
                                setInputValue('')
                                setPsdLgs([])
                                setSpan('hidden')
                              }
}
  const onSelectLg = e => {
                                setInputValue(e.target.innerHTML)
                                setPsdLgs([])
                                setSpan('hidden')
                                if(lgs.some(({name})=> name === e.target.innerHTML) ){
                                  setLocalGovtId(e.target.dataset.id)
                                  setLocalGovt(e.target.innerHTML.toLowerCase())
                                }
                              }
  const canSubmit = Boolean(localGovt)
  const onSubmitLG = () =>{
                            dispatch(updateCandidateLocalGovtId(localGovtId))
                            dispatch(updateCandidateLocalGovt(localGovt))
                          }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
    if(!lgs.length>0){  
                          const init = {
                                          method: 'GET',
                                          headers:  {
                                                      'Content-Type':'application/x-www-form-urlencoded',
                                                      'authorization': `Bearer ${authToken}`
                                                    }
                                        }
                          fetch(`${process.env.ServerURL}/localgovt/sr/${candidate.stateId}`, init)
                          .then((response)=> response.json())
                          .then(flgs => {
                              setLgs(flgs)
                          })
                          .catch(error => {
                                            setError(error)
                                        })
                          
                        }
  })
  const parsedLgs = psdLgs.map(lg =>(
    <span className="parsedobjects-items" key={lg._id} data-id={lg._id} type="button"
    onClick = {onSelectLg}
    >
      {lg.name}
    </span>
  ))
  return(
    <div
      id="form-body"
      className="form-body">
      <h1 className="form-header">Which local government is candidate emerging from emerging from?</h1>
      <div className="form-section">
      <input
        className="form-control mx-2"
        onChange={onLGChange}
        value={inputValue}
      />
      <button
        type="button"
        className="btn btn-success mx-2"
        onClick = {onSubmitLG}
        disabled = {!canSubmit}
        >Submit</button>
      </div>
      <div className="parsedobjects" hidden={span}>{parsedLgs}</div>
    </div>
  )
}
