import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useGetCandCountQuery } from '../api/apislice'


export const Candidates = () => {
  const [candCount, setCandCount] = useState(0)
 
  const {
    data: cands,
    isLoading,
    isSuccess,
    isError,
    error
} = useGetCandCountQuery()

useEffect(() => {
    if(isSuccess){
                    setCandCount(cands.candidatecount)
    }
})
  return(
          <div className="poll-list">
            <div>
              <h4>Candidates</h4>
            </div>
            <div className="dash-numbers">
            <Link to="/candidate/dashboard"><h2>{candCount}</h2></Link>
            </div>
          </div>
        )
}
