import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useGetUserListQuery } from '../api/apislice'
import { Spinners } from '../../app/spinner'


export const UsersDash = () => {
  const {
          data: userlist = [], 
          isLoading, 
          isSuccess, 
          isError,
          error
        } = useGetUserListQuery()
  const [users, setUsers] = useState([])
  const [psdUsers, setPsdUsers] = useState([])
  const [inputValue, setInputValue] = useState('')
  useEffect(()=>{
    if(isSuccess){
                    setPsdUsers(userlist)
                    setUsers(userlist)
                  }
    else if(isError){
      console.log(`${error.status}: ${JSON.stringify(error.data)}`)
    }
  })  
  const onUserselect = e =>{
                              console.log(e.target.value)
                            }
  const onUserSrc = e => {
    
    if(e.target.value.length > 0){
                                    setInputValue(e.target.value)
                                    const regex = new RegExp(`^${e.target.value}`, 'i')
                                    const foundCIs = userlist.filter(({nickname}) => nickname.match(regex))
                                    setPsdUsers(foundCIs)
                                }else{
                                    setPsdUsers(userlist)
                                    setInputValue('')
                                }

    }


  
    const content = psdUsers.map((user, i)=>(
                                                <div className="row mb-2" key={user._id}>
                                                    <div className="col-sm-1 text-capitalize">
                                                      <input
                                                        className="form-check-input"
                                                        type="checkbox"
                                                        id={user._id}
                                                        value={user._id}
                                                        name={user.office}
                                                        onChange={onUserselect}
                                                      />
                                                    </div>
                                                    <div className="col-sm-1 text-capitalize">
                                                      {i+=1}
                                                    </div>
                                                    <div className="col-sm-2 text-capitalize">
                                                      {user.firstName}
                                                    </div>
                                                    <div className="col-sm-2 text-capitalize">
                                                      {user.lastName} 
                                                    </div>
                                                    <div className="col-sm-2 text-capitalize">
                                                      {user.nickname} 
                                                    </div>
                                                    <div className="col-sm-1 text-capitalize">
                                                      {user.profilecomplete ? 'Active': 'Inactive'}
                                                    </div>
                                                    <div className="col-sm-1 text-capitalize">
                                                      {user.respondent ? 'Respondent': 'User'}
                                                    </div>
                                                    <div className="col-sm-2">
                                                      <Link to={`/user/view/${user._id}`}>
                                                        <div
                                                        className='btn btn-info btn-sm w-100'
                                                        >
                                                          View
                                                        </div>
                                                      </Link>
                                                    </div>
                                                </div>
                                                )
                                              )

  return (
            <section className="poll-list">
              <div className="leadtitle">
                <h1>Users Board</h1>
              </div>
              <div className="dashboard-block">
                <div className="container">
                  <div className="d-flex flex-row justify-content-center mb-5">
                   <div className="col-sm-4">
                      <input
                        className="form-control mx-2"
                        placeholder="Search Polls"
                        onChange={onUserSrc}
                        value={inputValue}
                      />
                    </div>
                    <div className="col-sm-4">
                      <Link to = "/dashboard">
                      <div 
                        className="btn btn-info"
                        
                        >Go to Dashboard</div>
                      </Link>
                    </div>
                  </div>
                  {content}
                </div>
              </div>
            </section>
          )
}
