import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'


const initialState = {
                        pollitpoll  : {
                                        activepolls:0,
                                        pollcandidates:0,
                                        activepoll: []
                                      },
                        token       : null,
                        status      : 'idle',
                        error       : null
                      }


export const fetchPolls = createAsyncThunk('pollitpoll/fetchPolls', async (id, {getState, dispatch}) => {
    let token =  getState().auth.token 
      const init =  {
                      method: 'GET',
                      headers:  {
                                  'authorization': `Bearer ${token}`
                                }
                    }
        const response = await fetch(`${process.env.ServerURL}/fetchpolls/${id}`, init)
          if (response.ok){
            const jsonValue = await response.json()
            return Promise.resolve(jsonValue)
          }
})


const pollsSlice = createSlice({
  name: 'polls',
  initialState,
  reducers: {
    purgepoll:{
      reducer(state, action){
                              state.polls.pollitpoll.activepoll = []
                              }
      }
    },

  extraReducers(builder) {
    builder
      .addCase(fetchPolls.pending, (state, action) => {
        state.status = 'loading'
      })
      .addCase(fetchPolls.fulfilled, (state, action) => {
        state.status = 'succeeded'
        // Add any fetched posts to the array
        state.pollitpoll.activepoll.concat(action.payload)
      })
      .addCase(fetchPolls.rejected, (state, action) => {
        state.status = 'failed'
        state.error = action.error.message
      })
  }
                                })

export const { purgepoll } = pollsSlice.actions
export const activePolls = state => state.polls.pollitpoll.activepoll
export const status = state => state.polls.status
export const errorStatus = state => state.polls.error


export default pollsSlice.reducer
