import React, { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { useAuth0 } from "@auth0/auth0-react";
import { Link } from 'react-router-dom'
import { createAdmin, loggedAdmin } from '../features/users/usersSlice'
import { ProfileMenu } from './profileMenu'
import { getToken } from '../features/auth/authSlice'
const audienceId = process.env.AUDIENCE
import  Home  from '../features/auth/authbutton'


export const Navbar = () => { 
  const dispatch = useDispatch()
  const admin = useSelector(loggedAdmin)
  const { user, isAuthenticated, isLoading, getAccessTokenSilently } = useAuth0();
  useEffect(async () =>  {
    if (!isAuthenticated) {
            dispatch(getToken(null))
    } else {
              const token = await getAccessTokenSilently({
                                                            audience: audienceId,
                                                            scope: "read:pollitadminuser write:pollitadminuser"
                                                          })
            dispatch(getToken(token))
            if (token){
                        const uid  = user.sub.slice(6)
                        const initialUser = {
                                              uid,
                                              nickname: user.nickname,
                                              verifiedEmail: user.email_verified,
                                              email: user.name
                                            }
                        if(!admin.hasOwnProperty('uid')){
                          dispatch(createAdmin(initialUser))
                        }
                      }
    }
  }) 
  const utag = admin.hasOwnProperty('uid')  ? admin.nickname : null
  let Nav;
  if (!isAuthenticated ){
     Nav = (
             <ul className="nav d-flex justify-content-end">
               <li className="nav-item">
                 <Home/>
               </li>
             </ul>
           )
   }else{
     Nav = (
             <ul className="nav d-flex align-items-center">
               <li className="nav-item">
                 <Link to="/dashboard" className="text-dark text-dark text-decoration-none">
                    <div className="mx-2">
                      Dashboard
                    </div>
                 </Link>
               </li>
               <li className="nav-item">
                 <div className="d-flex">
                   <div className="dropdown mr-1" id="dropdowndiv">
                       <a type="button" className="nav-link p-2 mr-5 dropdown-toggle text-capitalize text-dark" id="dropdownMenuOffset" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" data-offset="10,20">
                         {utag}
                       </a>
                       <ProfileMenu user={admin}/>
                   </div>
                 </div>
               </li>
             </ul>
           )
   }
  return (
    <nav className="navbar navbar-expand-large navbar-light"  id="navalign">
      <Link to='/' className="navbar-brand text-dark">
        Pollitng
      </Link>
      {Nav}
    </nav>
  )
}
