import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { updateCandidateState, updateCandidateStateId } from './candidatesSlice'
import { selectToken } from '../auth/authSlice'

export const State = () =>{
  const [state, setState] = useState('')
  const authToken = useSelector(selectToken)
  const [stateId, setStateId] = useState('')
  const [span, setSpan] = useState('hidden')
  const [states, setStates] = useState([])
  const [psdstates, setPsdstates] = useState([])
  const [inputValue, setInputValue] = useState('')
  const [error, setError] = useState({})
  const dispatch = useDispatch()
  const onStateChange = e => {
    if(e.target.value.length > 0){
                                    setInputValue(e.target.value)
                                    setSpan('')
                                    const regex = new RegExp(`^${e.target.value}`, 'i')
                                    const foundStates = states.filter(({name}) => name.match(regex))
                                    setPsdstates(foundStates)
                                    if(states.some(({name})=> name.toLowerCase() === e.target.value.toLowerCase())){
                                      setStateId(foundStates[0]._id)
                                      setState(e.target.value.toLowerCase())
                                      setPsdstates([])
                                    }else{
                                      setInputValue(e.target.value)
                                      setState('')
                                    }
                                  }else{
                                        setInputValue('')
                                        setPsdstates([])
                                        setSpan('hidden')
                                      }
                                }
  const onSelectState = e => {
                                setInputValue(e.target.innerHTML)
                                setPsdstates([])
                                setSpan('hidden')
                                if(states.some(({name})=> name === e.target.innerHTML)){
                                  setStateId(e.target.dataset.id)
                                  setState(e.target.innerHTML.toLowerCase())
                                }
                              }
  const canSubmit = Boolean(state)
  const onSubmitState = () =>{
    dispatch(updateCandidateStateId(stateId))
    dispatch(updateCandidateState(state))
  }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
    if(!states.length>0){
                            const init = {
                              method: 'GET',
                              headers:  {
                                          'Content-Type':'application/x-www-form-urlencoded',
                                          'authorization': `Bearer ${authToken}`
                                        }
                            }
                          fetch(`${process.env.ServerURL}/states`, init)
                          .then((response)=> response.json())
                          .then(fstates => {
                              setStates(fstates)
                          })
                          .catch(error => {
                                                setError(error)
                                            })
                        }
  })
  const parsedState = psdstates.map(sts =>(
    <span className="parsedobjects-items" key={sts._id} data-id={sts._id} type="button"
    onClick = {onSelectState}
    >
      {sts.name}
    </span>
  ))
  return(
    <div
      id="form-body"
      className="form-body">
      <h1 className="form-header">Which state is candidate emerging from?</h1>
      <div className="form-section">
      <input
        className="form-control mx-2"
        onChange={onStateChange}
        value={inputValue}
      />
      <button
        type="button"
        className="btn btn-success mx-2"
        onClick = {onSubmitState}
        disabled = {!canSubmit}
        >Submit</button>
      </div>
      <div className="parsedobjects" hidden={span}>{parsedState}</div>
    </div>
  )
}
