import React from 'react'
import {
  BrowserRouter as Router,
} from 'react-router-dom'
import { Routes } from 'react-router-dom'
import { Route } from 'react-router-dom'
import { useNavigate } from 'react-router-dom'
import { withAuthenticationRequired } from '@auth0/auth0-react'
import { Auth0Provider } from '@auth0/auth0-react'
import { Navbar } from './app/Navbar'
import { Dashboard } from './features/admin/dashboard'
import { CandSignUp } from './features/candidates/candSignup'
import { CandDash } from './features/candidates/canddash'
import { BlogsDash } from './features/blogs/blogsdash'
import { BlogView } from './features/blogs/blogview'
import { LandingPage } from './features/admin/landingPage'
import { NewParty } from './features/party/newparty'
import { PartyDash } from './features/party/partydash'
import { PollsCreate } from './features/polls/pollscreate'
import { PollsDash } from './features/polls/pollsdash'
import { PollView } from './features/polls/pollview'
import Profile from './features/users/profile'
import { createBrowserHistory } from 'history'
import { RespondentDash } from './features/users/respondentdash'
import { UsersDash } from './features/users/usersdash'
import { UserProfile } from './features/users/userprofile'
//let history = createBrowserHistory();
//let location = history.location;
//let unlisten = history.listen(({ location, action }) => {});
const domain = process.env.OKTADOMAIN
const clientId = process.env.CLIENTID
const audienceId = process.env.AUDIENCE
const ProtectedRoute = ({ component, ...args }) => {
  const Component = withAuthenticationRequired(component, args);
  return <Component />;
}
const Auth0ProviderWithRedirectCallback = ({ children, ...props }) => {
  const navigate = useNavigate();
  const onRedirectCallback = (appState) => {
    navigate((appState && appState.returnTo) || window.location.pathname);
  };
  return (
    <Auth0Provider onRedirectCallback={onRedirectCallback} {...props}>
      {children}
    </Auth0Provider>
  );
}
const Main = () => {
  return(
          <React.Fragment>
            <Dashboard/>
          </React.Fragment>
  )
}
function App(){
                return (
                        <Router>
                          <Auth0ProviderWithRedirectCallback
                            domain={domain}
                            clientId={clientId}
                            redirectUri={window.location.origin}
                            audience={audienceId}
                            scope="read:pollitadminuser write:pollitadminuser"
                          >
                            <Navbar/>
                            <Routes>
                            <Route
                              exact 
                              path="/dashboard"
                              element={<ProtectedRoute component={Main}/>}
                            />
                            <Route exact path="/" element={<LandingPage/>}/>
                            <Route exact path="/blogs/dashboard" element={<ProtectedRoute component={BlogsDash}/>}/>
                            <Route exact path="/blogs/view/:blogid" element={<ProtectedRoute component={BlogView}/>}/>
                            <Route exact path="/candidate/create" element={<ProtectedRoute component={CandSignUp}/>}/>
                            <Route exact path="/candidate/dashboard" element={<ProtectedRoute component={CandDash}/>}/>
                            <Route exact path="/party/create" element={<ProtectedRoute component={NewParty}/>}/>
                            <Route exact path="/party/dashboard" element={<ProtectedRoute component={PartyDash}/>}/>
                            <Route exact path="/polls/create" element={<ProtectedRoute component={PollsCreate}/>}/>
                            <Route exact path="/polls/dashboard" element={<ProtectedRoute component={PollsDash}/>}/>
                            <Route exact path="/polls/view/:pollId/:officeScope" element={<ProtectedRoute component={PollView}/>}/>
                            <Route path="/profile" element={<ProtectedRoute component={Profile}/>}/>
                            <Route path="/users/dashboard" element={<ProtectedRoute component={UsersDash}/>}/>
                            <Route path="/respondent/dashboard" element={<ProtectedRoute component={RespondentDash}/>}/>
                            <Route path="/user/view/:userid" element={<ProtectedRoute component={UserProfile}/>}/>
                            </Routes>
                          </Auth0ProviderWithRedirectCallback>
                        </Router>
                      )
              }

export default App
